<template>
    <div class="newmv">
        <video controls autoplay :src="{urls}" ></video>
        <p>
            {{item.name}}
        </p>
    </div>
</template>
<script>
export default {
    props: {
    item: Object,
  },
  data(){
      return{
          urls:null
      }
  },
  mounted() {
      console.log(this.item.id)
  },
  created() {
    this.axios.get("/mv/url?id="+this.item.id).then((data) => {
      this.urls=data.data.data.url;
      console.log(this.urls)
    });
  },
}
</script>
<style lang="less" scoped>
    .newmv{
        video{
            width: 100%;
           
        }
        p{
            margin: 5px;
        }
    }
</style>